import React from "react";
import styled from 'styled-components';


const ContentFooter = styled.h1`
    font-size: 2em;
    color:#fff;
    text-align:right;
    padding: 25px 0 0 0;
    font-family: 'Raleway', sans-serif;
`;

const ContentFooterBg = styled.footer`
background: #4B0D2B;
position: absolute;
z-index: -1;
height: 90px;
width: 100%;
bottom: 2px;
`;

const ContentSocial = styled.ul`
  display:flex;
  padding: 25px 0 0 0;

`;

const HandCrafted = styled.p`
  display: flex;
  justify-content: center;
  color:#fff;
  padding: 10px 0 5px 0;
  font-family: 'Raleway', sans-serif;
  

`;

const SocialIcos = styled.a`
  color:#fff;
  font-size:2.5em;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  
  &:hover {
      transition:all 350ms ease-in;
      color:#18529b;
      -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  }

`;

class Footer extends React.Component {
    render() {
        return (
            <ContentFooterBg className="BottomDiv ">
                <div className="grid-container">
                    <div className="grid-x grid-margin-x">
                        <div className="cell small-6 large-5">
                            <ContentFooter>Follow me on:</ContentFooter>
                        </div>
                        <div className="cell small-6 large-7">
                            <ContentSocial className="socials">
                                <li><SocialIcos href="" className="fab fa-github"></SocialIcos></li>
                                <li><SocialIcos href="" className="fab fa-gitlab"></SocialIcos></li>
                                <li><SocialIcos href="" className="fab fa-bitbucket"></SocialIcos></li>
                                <li><SocialIcos href="" className="fab fa-instagram"></SocialIcos></li>
                                <li><SocialIcos href="" className="fab fa-linkedin"></SocialIcos></li>
                                <li><SocialIcos href="" className="fab fa-twitter"></SocialIcos></li>
                                <li><SocialIcos href="" className="far fa-envelope"></SocialIcos></li>
                            </ContentSocial>
                        </div>
                    </div>
                    <HandCrafted>Handcrafted by me &#9400; ivans.tech</HandCrafted>
                </div>
            </ContentFooterBg>
        );
    };
}

export default Footer;


