import React from "react";
import Particles from 'react-particles-js';
import Typewriter from './Typewriter';


class Preloder extends React.Component {

    render() {
        return (
            <div >
                <Particles params={{
                    particles: {
                        number: {
                            value: 50
                        },
                        line_linked: {
                            shadow: {
                                enable: false,
                                color: "#3CA9D1",
                                blur: 5
                            }
                        }
                    }
                }}
                    style={{
                        position: "fixed",
                        top: 0,
                        left: 0,
                        width: "100%",
                        height: "100%"
                    }} />
                <Typewriter />
            </div>
        );

    }

}

export default Preloder;


