import React from "react";
import styled from 'styled-components';


const HeadingStyles = styled.h1`
  font-size: 3em;
  text-align: center;
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .1em solid orange; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em; /* Adjust as needed */
  font-style:italic;
  font-family: 'Cabin Sketch', cursive;
  animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
    font-family: 'VT323', monospace;
`;

const WritterStylesDiv = styled.div`
    position: absolute;
    top: 50vh;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#fff;
`;

class Typewriter extends React.Component {
    render() {
        return (
            <WritterStylesDiv className="typewriter">
                <HeadingStyles>Hello !, Welcome to my Front end Developer's world ;-)!</HeadingStyles>
            </WritterStylesDiv>
        );
    };
}

export default Typewriter;


