(() => { 

  setTimeout(function(){ 
        const preloderBg = document.querySelector('.preloder');
        const body = document.getElementsByTagName("body")[0];
        const bottomDiv = document.querySelector(".BottomDiv");
       const TopSection = document.querySelector('.TopSection');
       const waveWrapper = document.querySelector('.waveWrapper');
       const bighead = document.querySelector('.bighead');
       const smallHead = document.querySelector('.smallHead');

       bighead.classList.add('lightSpeedIn');
       smallHead.classList.add('rubberBand');
       bottomDiv.classList.add('staticElement');
       waveWrapper.classList.add('staticElement');
        preloderBg.style.display = "none";
       bottomDiv.classList.add('staticElement');
        TopSection.classList.add('abosoluteelemen');
        body.classList.add('newBody');






        
    }, 10000);
  


 })();


