import React from "react";
import styled from 'styled-components';



const IntroSettings = styled.div`
position: absolute;
z-index: 99;
color: #fff;
top: 0;
left: 0;
right: 0;
bottom: 0;
display: -webkit-box;
display: -webkit-flex;
display: -ms-flexbox;
display: flex;
-webkit-flex-direction: column;
-ms-flex-direction: column;
flex-direction: column;
-webkit-box-pack: center;
-webkit-justify-content: center;
-ms-flex-pack: center;
justify-content: center;
margin: 13% auto;
text-align: center;
height: 400px;
`;

const BigHeading = styled.h2`
font-family: 'Raleway', sans-serif;
font-size:5.5em;

`;

const SmallHead = styled.p`
font-family: 'Raleway', sans-serif;
font-size:2.2em;
margin:0 auto;
`;

const ButtonMainPage = styled.a

`  
font-family: 'Raleway', sans-serif;
color: #fff;
background: #4B0D2B;
width: 20%;
margin: 35px auto;
text-align: center;
font-size: 2.5em;
text-transform: uppercase;
font-style: italic;
padding: 10px;
border-radius: 30px;

&:hover {
  transition:all 350ms ease-in;
}

`
;

class Mainsection extends React.Component {
  render() {
    return (
      <div className="waveWrapper waveAnimation">
        <IntroSettings className="introduce">
          <BigHeading className="bighead animated ">Marcin Iwańczyk</BigHeading>
          <SmallHead className="smallHead animated ">creative front-end developer</SmallHead>
          <ButtonMainPage>view portfolio</ButtonMainPage>
        </IntroSettings>
        <div className="waveWrapperInner bgTop">
          <div className="wave waveTop"></div>
        </div>
        <div className="waveWrapperInner bgMiddle">
          <div className="wave waveMiddle"></div>
        </div>
        <div className="waveWrapperInner bgBottom">
          <div className="wave waveBottom" ></div>
        </div>
      </div>
    );
  };
}

export default Mainsection;


