import React from "react";
import Preloder from './Preloder';
import Header from './Header';
import Mainsection from './Mainsection';
import Footer from './Footer';




class App extends React.Component {

    render() {
        return (
            <div className="whole">

            <div className="preloder">
               
            <Preloder/> 
                

            </div>

            <Header/>
            <Mainsection/>
        <Footer />
        </div>
    

        );

    }

}

export default App;


