import React from "react";
import styled from 'styled-components';
import Hello from './HelloComponent';
import About from './AboutComponent';
import Projects from './Projects';
import Skills from './Skills';
import Contact from './Contact';

import {
    BrowserRouter as Router,
    Route,
    Link,
    Switch,
    Redirect
} from 'react-router-dom';


const TopSection = styled.div`
  position:absolute;
  z-index:-1;
  max-width: 75rem;
  width: 75rem;

`;

const MenuSetting = styled.ul`
    display:flex;
    justify-content: flex-end;
    margin: 20px 0 21px 0;
    color:#fff;
`;

const MenuSettingItem = styled.a `
    font-size:2em;
    color:#000;
    font-family: 'Raleway', sans-serif;
`;

const ResImage = styled.img `

    max-width:100%;
    width:80%;
    margin: -10px 0 21px 0;

`;

class Header extends React.Component {

    render() {
        return (
            <Router>
                <div className="grid-container">

                    <TopSection className="App TopSection">
                        <div className="grid-x grid-margin-x">
                            <div className="cell small-4 large-4">
                                <a href=""><ResImage src="../images/logosite.png" alt=""></ResImage></a>
                            </div>
                            <div className="cell small-4 large-8">
                                <div className="container">
                                    <MenuSetting>
                                        <li><MenuSettingItem><Link to="/">Home</Link></MenuSettingItem></li>
                                        <li><MenuSettingItem><Link to="/about">About</Link></MenuSettingItem></li>
                                        <li><MenuSettingItem><Link to="/skills">Skills</Link></MenuSettingItem></li>
                                        <li><MenuSettingItem><Link to="/projects">Projects</Link></MenuSettingItem></li>
                                        <li><MenuSettingItem><Link to="/contact">Contact</Link></MenuSettingItem></li>
                                    </MenuSetting>
                                    <Route path="/" />
                                    <Route path="/about" component={About} />
                                    <Route path="/skills" component={Skills} />
                                    <Route path="/projects" component={Projects} />
                                    <Route path="/contact" component={Contact} />
                                </div>
                            </div>
                        </div>
                    </TopSection>

                </div>
            </Router>
        );

    }

}

export default Header;


